.container {
	margin: 3em;
}

#earth {
	width: 180px;
}

#satellite {
	width: 50px;
}

.satellite-move {
	-webkit-animation: sat-move;
	        animation: sat-move;
	-webkit-animation-duration: 10s;
	        animation-duration: 10s;
	-webkit-animation-iteration-count: infinite;
	        animation-iteration-count: infinite;
	position: relative;
    top: 175px;
    left: -110px;
}

.satellite-rotate {
	-webkit-animation: sat-rotate;
	        animation: sat-rotate;
	-webkit-animation-duration: 5s;
	        animation-duration: 5s;
	-webkit-animation-iteration-count: infinite;
	        animation-iteration-count: infinite;
}

@-webkit-keyframes sat-move {
	0% { -webkit-transform: translate(0px, 0px); transform: translate(0px, 0px); z-index: 1; }
	50% { -webkit-transform: translate(200px, -175px); transform: translate(200px, -175px) } 
	70% { z-index: -1; } 
	100% { -webkit-transform: translate(0px, 0px); transform: translate(0px, 0px); z-index: -1; }
}

@keyframes sat-move {
	0% { -webkit-transform: translate(0px, 0px); transform: translate(0px, 0px); z-index: 1; }
	50% { -webkit-transform: translate(200px, -175px); transform: translate(200px, -175px) } 
	70% { z-index: -1; } 
	100% { -webkit-transform: translate(0px, 0px); transform: translate(0px, 0px); z-index: -1; }
}

@-webkit-keyframes sat-rotate {	
	0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
	100% { -webkit-transform: rotate(180deg); transform: rotate(180deg) }
}

@keyframes sat-rotate {	
	0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
	100% { -webkit-transform: rotate(180deg); transform: rotate(180deg) }
}




