@-webkit-keyframes particleAnimation {
    from {
        left: -100px;
    }
    to {
        left: calc( 100% + 100px );
    }
}

@keyframes particleAnimation {
    from {
        left: -100px;
    }
    to {
        left: calc( 100% + 100px );
    }
}

.p {
  position:fixed;
  left:0px;
  top:50px;
  width:1px;
  height:1px;
  background-color:white;
  position:fixed;
  z-index: 1;
  -webkit-animation-name:particleAnimation;
          animation-name:particleAnimation;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}

.p::before {
  position:absolute;
  display:block;
  content:"";
  width:100px;
  right:1px;
  top:0px;
  height:1px;
  background: -webkit-gradient(linear, left top, right top, from(rgba(0,0,0,0)),to(rgba(255,255,255,0.4)));
  background: -webkit-linear-gradient(left, rgba(0,0,0,0) 0%,rgba(255,255,255,0.4) 100%);
  background: -o-linear-gradient(left, rgba(0,0,0,0) 0%,rgba(255,255,255,0.4) 100%);
  background: linear-gradient(to right, rgba(0,0,0,0) 0%,rgba(255,255,255,0.4) 100%); 
}

.p-1 {
  -webkit-animation-duration: 5s;
          animation-duration: 5s;
  top: 40%;
}

.p-2 {
  -webkit-animation-duration: 10s;
          animation-duration: 10s;
  top: 70%;
}


